import React, { Component } from 'react';

let navigatorId;
let initVal = 0;
let lineX, lineY, lineZ = 0;

class Geolocation extends Component {
	constructor(props) {
	    super(props);
	    this.state = {
	    	list: [],
	    	latestPoint: {
	    		lat: initVal.toFixed(6),
	    		lng: initVal.toFixed(6)
	    	},
	    	accelValues: {
	    		x: 0,
	    		y: 0,
	    		z: 0
	    	},
	    	context: null
	    };

	    this.canvas = React.createRef();
	}

	componentDidMount = () => {
		this.setState({
  			list: [...this.state.list, 
  			{
  				lat: this.state.latestPoint.lat, 
  				lng: this.state.latestPoint.lng,
  				x: this.state.accelValues.x,
  				y: this.state.accelValues.y,
  				z: this.state.accelValues.z
  			}]
  		});

  		const canvas = this.canvas.current;
  		this.setState({
  			context: canvas.getContext('2d')
  		})
    	//this.state.context = 
	}

	startRecording = () => {
		//localStorage.removeItem("track");
		if ('geolocation' in navigator) {
	    	navigatorId = navigator.geolocation.watchPosition(this.updateLatestPoint);

	    	if (window.DeviceMotionEvent) {
				window.addEventListener("devicemotion", this.updateAccelValues);
			} 
		}	
	}

	updateAccelValues = (ev) => {
		// var accel = ev.acceleration;
		// if (accel.x > this.state.accelValues.x + 0.1 || accel.x > this.state.accelValues.x - 0.1) {
			if (ev.acceleration.x !== null && ev.acceleration.y !== null && ev.acceleration.z !== null) {
				this.setState({
					accelValues: {
						x: ev.acceleration.x.toFixed(4),
						y: ev.acceleration.y.toFixed(4),
						z: ev.acceleration.z.toFixed(4)
					}
				}, () => {
					this.drawLine(this.state.accelValues.x, lineX, 1);
					this.drawLine(this.state.accelValues.y, lineY, 2);
					this.drawLine(this.state.accelValues.z, lineZ, 3);
					lineX++;
					lineY++;
					lineZ++;
				//var ctx = c.getContext("2d");
				// this.state.context.moveTo(lineNum, ev.acceleration.x * 10);
				// this.state.context.lineTo(lineNum, 400);
				// this.state.context.stroke();
				// lineNum++;
			});
			}
		// 	}, () => this.updateList());
		// }
		
	}

	drawLine = (value, line, index) => {
		var axis = index * 100 - 50;
		this.state.context.moveTo(line, axis + value * 10);
		this.state.context.lineTo(line, axis);
		this.state.context.stroke();
	}

	updateLatestPoint = (location) => {
		this.setState({
			latestPoint: {
				lat: location.coords.latitude.toFixed(6),
				lng: location.coords.longitude.toFixed(6)
			}
		});

		// , () => {
		// 		//var ctx = c.getContext("2d");
		// 		this.state.context.moveTo(lineNum, 100);
		// 		this.state.context.lineTo(lineNum, 400);
		// 		this.state.context.stroke();
		// 		lineNum++;
		// 	});
	}

	updateList = () => {
		this.setState({
  			list: [...this.state.list, 
  			{
  				lat: this.state.latestPoint.lat, 
  				lng: this.state.latestPoint.lng,
  				x: this.state.accelValues.x,
  				y: this.state.accelValues.y,
  				z: this.state.accelValues.z
  			}]}, () => console.log(this.state.list));
	}

	stopRecording = () => {
		localStorage.setItem("track", JSON.stringify(this.state.list));
		navigator.geolocation.clearWatch(navigatorId);
		window.removeEventListener("devicemotion", this.updateAccelValues);
	}

	  render() {
	    return (
	    <div>
	    	<button onClick={this.startRecording}>Start recording</button>
	    	<div>lat={this.state.latestPoint.lat}, lng={this.state.latestPoint.lng} </div>
	    	<div>x={this.state.accelValues.x}, y={this.state.accelValues.y}, z={this.state.accelValues.z}</div>
	    	<canvas id="canvas" width="400" height="300" ref={this.canvas}/>
	    	<button onClick={this.stopRecording}>Stop recording</button>
	    </div>
	    );
	  }
}

export default Geolocation;

//<ul>{this.state.list.map((coords, i) => (<li key={i}>Current position: lat = {coords.latitude}, long = {coords.longitude}</li>))}</ul>

